<template>
    <div class="about">
        <v-toolbar color="secondary" flat>
            <v-toolbar-side-icon @click="backHome">
                <v-icon color="white">arrow_back</v-icon>
            </v-toolbar-side-icon>
            <v-toolbar-title class="white--text">nVision</v-toolbar-title>
        </v-toolbar>
        <div v-if="created">

            <div v-if="notFound" class="container-details">
                Container <strong>{{container.id}}</strong> was not found.
            </div>
            <div v-if="!notFound" class="container-details">
                <label>properties for</label>
                <h1>{{container.id}}</h1>
                <hr class="separator"/>
                <label>Weight (kg)</label>
                <h2>{{container['Weight (kg)'] || '--'}}</h2>
                <label>Type ISO</label>
                <h2>{{container['Type ISO'] || '--'}}</h2>
                <label>Category</label>
                <h2>{{container['Category'] || '--'}}</h2>
                <label>Position</label>
                <h2>{{container['Position'] || '--'}}</h2>
                <label>Line Op</label>
                <h2>{{container['Line Op'] || '--'}}</h2>
                <label>POL</label>
                <h2>{{container['POL'] || '--'}}</h2>
                <label>POD</label>
                <h2>{{container['POD'] || '--'}}</h2>
                <label>Frght Kind</label>
                <h2>{{container['Frght Kind'] || '--'}}</h2>
                <label>O/B Actual Visit</label>
                <h2>{{container['O/B Actual Visit'] || '--'}}</h2>
                <label>Vessel ATA</label>
                <h2>{{container['Vessel ATA'] || '--'}}</h2>
                <label>Vessel ATD</label>
                <h2>{{container['Vessel ATD'] || '--'}}</h2>
                <label>Reqs Power</label>
                <h2>{{container['Reqs Power'] || '--'}}</h2>
                <label>Hzd UNNbrs</label>
                <h2>{{container['Hzd UNNbrs'] || '--'}}</h2>
                <label>Hazardous</label>
                <h2>{{container['Hazardous?'] || '--'}}</h2>
                <label>Temp Required (C)</label>
                <h2>{{container['Temp Required (C)'] || '--'}}</h2>
                <label>IMDG</label>
                <h2>{{container['IMDG'] || '--'}}</h2>
                <label>Damages</label>
                <h2>{{container['Damages'] || '--'}}</h2>
                <h2 style="display:flex">
                    <span style="flex: 1;"></span>
                    <v-flex xs12 sm6 md3>
                        <v-text-field
                                v-model="damageDesc"
                                label="Add damage"
                                placeholder="describe"
                        ></v-text-field>
                    </v-flex>
                    <v-btn @click="addDamage" icon>
                        <v-icon>add</v-icon>
                    </v-btn>
                </h2>
                <!--
                <label>Damage Images</label>
                <h2 style="display:flex">{{container['Damage Images'] || '--'}}
                </h2>
                -->
            </div>
        </div>
    </div>
</template>

<script>
import dataService from '../services/dataService';

export default {
  name: 'Container',
  components: {},
  created() {
    this.container.id = this.$route.params.id;

    dataService.getData()
      .get()
      .then((snapshot) => {
        this.notFound = true;
        snapshot.forEach((doc) => {
          if (doc.data().id === this.container.id) {
            console.log(doc.data());
            this.container = doc.data();
            this.notFound = false;
            this.docId = doc.id;
          }
        });
        this.created = true;
      });
  },
  methods: {
    backHome() {
      this.$router.push({
        name: 'home',
      });
    },
    addDamage() {
      dataService.getData().doc(this.docId).update({Damages: this.damageDesc});
      this.container.Damages = this.damageDesc;
      this.damageDesc = '';
    }
  },
  data() {
    return {
      docId: '',
      container: {},
      containers: [],
      notFound: false,
      created: false,
      damageDesc: '',
    };
  },
};
</script>

<style scoped>
    .container-details {
        text-align: left;
        padding: 8px;
    }

    h1 {
        font-size: 30px;
        font-weight: normal;
    }

    h2 {
        font-size: 26px;
        font-weight: normal;
    }

    .separator {
        margin-bottom: 16px;
    }
</style>
