<template>
    <div class="results">
        <v-toolbar color="secondary" flat>
            <v-toolbar-side-icon @click="backHome">
                <v-icon color="white">arrow_back</v-icon>
            </v-toolbar-side-icon>
            <v-toolbar-title class="white--text">nVision</v-toolbar-title>
        </v-toolbar>
        <div v-if="created">
            <div class="container">
                <label><strong>results found ({{filteredContainers.length}}):</strong></label>
                <v-text-field
                        hide-details
                        flat
                        single-line
                        placeholder="Find Containers"
                        v-model="query"
                        @input="updateQuery"
                ></v-text-field>
            </div>
            <div v-if="notFound" class="container-details">
                No containers found with the results.
            </div>
            <div v-if="!notFound" class="container-details">
                <div v-for="result in filteredContainers">
                    <div class="search-result" @click="() => goToResult(result.id)">
                        <div class="labels">
                            <div>Container</div>
                            <div>ISO</div>
                        </div>
                        <div class="values">
                            <h1>{{result.id}}</h1>
                            <h1>{{result['Type ISO'] || '--'}}</h1>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import dataService from '../services/dataService';

export default {
  name: 'SearchResults',
  created() {
    this.query = this.$route.params.query;
    this.updateQuery();
  },
  methods: {
    backHome() {
      this.$router.push({
        name: 'home',
      });
    },
    updateQuery(){
      this.filteredContainers = [];

      dataService.getData()
        .get()
        .then((snapshot) => {
          this.notFound = true;
          snapshot.forEach((doc) => {
            if (doc.data()
              .id.toLowerCase()
              .indexOf(this.query.toLowerCase()) > -1) {
              this.filteredContainers.push(doc.data());
              this.notFound = false;
            }
          });
          this.created = true;
        });
    },
    goToResult(id){
        this.$router.push({
          name: 'container',
          params: {
            id
          }
        });
    },
  },
  data() {
    return {
      query: '',
      filteredContainers: [],
      created: false,
      notFound: true,
    };
  }

};
</script>

