    .container {
        text-align: left;
        padding: 8px;
    }

    .search-result {
        text-align: left;
        border: 2px solid black;
        margin: 4px 8px;
        display: flex;
        border-radius: 8px;
        overflow: hidden;
        .labels {
            display: flex;
            flex-direction: column;
            padding: 8px;
            text-align: right;
            flex: 1;
            background: lightgray;
            div {
                flex: 1;
            }
        }
        .values {
            padding: 8px;
            flex: 3;
            display: flex;
            flex-direction: column;
            h1 {
                font-weight: normal;
                font-size: 30px;
                flex: 1;
            }
        }
    }
</style>
